.img-headerlogo-icon, .img-onespec-icon {
  object-fit: cover;
  align-self: stretch;
  width: 92px;
  max-height: 100%;
  position: relative;
}

.img-onespec-icon {
  width: 145px;
}

.header-left {
  justify-content: flex-start;
  align-self: stretch;
  align-items: center;
  gap: var(--gap-7xl);
  flex-direction: row;
  display: flex;
}

.dev {
  font-size: 14px;
  font-family: var(--font-noto-sans);
  color: var(--color-white);
  text-align: left;
  position: relative;
}

.btn-lsnmall {
  cursor: pointer;
  padding: var(--padding-9xs) var(--padding-3xs);
  background-color: var(--color-black);
  border: 0;
  border-radius: 13px;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  display: flex;
}

.container, .header2 {
  align-items: center;
  width: 100%;
  display: flex;
}

.container {
  box-sizing: border-box;
  flex-direction: row;
  justify-content: space-between;
  max-width: 1600px;
  height: 120px;
  padding: 42px 36px;
}

.header2 {
  background-color: var(--color-white);
  flex-direction: column;
  justify-content: flex-start;
  min-width: 370px;
  position: relative;
}

@media screen and (width <= 960px) {
  .header-left {
    gap: var(--gap-xl);
  }

  .dev {
    font-size: var(--font-size-xs);
  }

  .container {
    padding-left: var(--padding-11xl);
    padding-right: var(--padding-11xl);
    box-sizing: border-box;
  }
}

@media screen and (width <= 768px) {
  .header-left {
    gap: var(--gap-base);
  }

  .btn-lsnmall, .container {
    display: flex;
  }

  .container {
    padding: var(--padding-13xl) 26px;
    box-sizing: border-box;
    height: 80%;
  }
}

@media screen and (width <= 480px) {
  .img-headerlogo-icon {
    width: 62px;
  }

  .img-onespec-icon {
    width: 100px;
  }

  .header-left {
    align-self: stretch;
    gap: 6px;
    height: auto;
  }

  .dev {
    font-size: var(--font-size-xs);
  }

  .btn-lsnmall {
    padding-left: var(--padding-9xs);
    padding-right: var(--padding-9xs);
    box-sizing: border-box;
    display: none;
  }

  .container {
    box-sizing: border-box;
    gap: 972px;
    height: 56px;
    padding: 16px 13px;
  }
}

